import {
    BrowserRouter,
    Switch,
    Route
  } from "react-router-dom";
  import HeaderComponent from "./Components/HeaderComponent";
import Tables from "./Components/Tables";

export default function Router() {
    return (
      <BrowserRouter>
      <HeaderComponent />
        <Switch>
          <Route path="/">
            <Tables />
          </Route>
        </Switch>
      </BrowserRouter>
    )
}